@import url(https://fonts.googleapis.com/css?family=Permanent+Marker|Oswald|Press+Start+2P|Audiowide|Lobster);
*{box-sizing:border-box;text-decoration:none;list-style:none;margin:0;padding:0;text-align:center;font-family:"Oswald";background-color:inherit}html{font-size:62.5%;position:relative;height:100%;scroll-behavior:smooth}body{display:flex;flex-direction:column;height:100vh}button{transition:background-color 0.4s ease-in-out,color 0.4s ease-in-out;vertical-align:center;cursor:pointer;border-radius:1.6rem;color:#fff;text-transform:uppercase}.button-primary{background-color:#f90360;padding:0.7rem 1.8rem;font-size:1.2rem}.button-secondary{padding:0.2rem 1.1rem;font-size:1rem;background-color:#858989}button:hover{background-color:blue}h1{font-size:4rem;letter-spacing:0.8rem;font-weight:bold;text-transform:uppercase}h3{font-size:1.8rem}h4{font-size:1.6rem}p{font-size:1.2rem}i{cursor:pointer;font-size:2.5rem}img{cursor:pointer}span{cursor:pointer;font-size:1.1rem;letter-spacing:0.2rem;color:#8b8b8b;font-weight:bold;text-transform:uppercase}a{cursor:pointer;color:inherit}.social-media{display:flex;align-items:center;flex-flow:wrap row}.social-media>*{margin:0 0.2rem;transition:transform 0.3s ease-in-out}.social-media>*:hover{transform:scale(1.1)}@keyframes shimmer{0%{background-position:-468px 0}100%{background-position:468px 0}}nav{-webkit-backface-visibility:hidden;backface-visibility:hidden;min-height:18vh;width:100%;z-index:1}nav>:first-child{transition:background-color 0.4s ease-in-out,color 0.4s ease-in-out;min-height:4.4vh;background-color:#f1f1f1;border-bottom:2px groove #bdbdbd;display:flex;align-items:center;flex-flow:wrap row;justify-content:space-between;padding:0 2rem}nav>:first-child .social-media{width:33.3%}nav>:first-child .radio-tv-replay{display:flex;align-items:center;flex-flow:wrap row;width:33.3%;justify-content:center}nav>:first-child .radio-tv-replay>*{padding:0.9rem 1.5rem;text-transform:uppercase}nav>:first-child .radio-tv-replay>*:hover{color:blue}nav>:first-child .radio-tv-replay>:nth-child(2n){border-left:1px solid #000;border-right:1px solid #000}nav>:first-child .account{width:50%;flex:1 0 auto;display:flex;justify-content:space-evenly;flex-direction:row}nav>:first-child .right-nav{display:flex;justify-content:space-around;flex-direction:row;width:33.3%}nav>:last-child{transition:background-color 0.4s ease-in-out,color 0.4s ease-in-out;min-height:13.6vh;border-bottom:2px groove #bdbdbd;display:flex;align-items:center;flex-flow:wrap column;justify-content:space-evenly}nav>:last-child .SIP-logo{margin:0 auto;height:6rem;width:18rem}nav>:last-child .nav-menu{background-color:transparent;text-transform:uppercase;display:flex;align-items:center;flex-flow:wrap row;justify-content:center}nav>:last-child .nav-menu li>*{transition:opacity 0.3s ease-in-out}nav>:last-child .nav-menu:hover li:not(:hover):not(:last-child)>*{opacity:0.2}nav>:last-child .nav-menu>*{margin:0 2rem}nav>:last-child .fa-search{color:#a8adbf;font-size:1.5rem}nav .nav-menu,nav .radio-tv-replay,nav .account{color:#8b8b8b;font-weight:bold;letter-spacing:0.2rem}.hidden{display:none}.show{display:flex}@media only screen and (min-width: 768px){.theme-toggler .dark-mode-label{transform:scale(1.5)}}nav.dark-mode>:first-child{background-color:#370617}nav.dark-mode>:first-child .radio-tv-replay>*:hover{color:yellow}nav.dark-mode>:first-child .radio-tv-replay>:nth-child(2n){border-left:1px solid #fff;border-right:1px solid #fff}nav.dark-mode>:last-child{background-color:#000}nav.dark-mode .nav-menu,nav.dark-mode .radio-tv-replay{color:#fff}.theme-toggler{display:flex}.theme-toggler .dark-mode-checkbox{opacity:0;position:absolute}.theme-toggler .dark-mode-label{background-color:#111;border:1px solid #000;border-radius:5rem;cursor:pointer;display:flex;align-items:center;justify-content:space-between;padding:0.5rem;position:relative;height:2.5rem;width:5rem;transform:scale(1.2)}.theme-toggler .dark-mode-label>i:first-child{font-size:1.5rem;color:#f1c40f}.theme-toggler .dark-mode-label>i:nth-child(n+1){font-size:1.5rem;color:#f39c12}.theme-toggler .dark-mode-label .ball{background-color:#fff;border-radius:50%;position:absolute;top:0;left:0.1rem;height:95%;width:45%;transform:translateX(0);transition:transform 0.5s ease-in-out}.theme-toggler .dark-mode-checkbox:checked+.dark-mode-label .ball{transform:translateX(100%)}main{min-height:60vh;width:100%;display:flex;align-items:center;flex-flow:wrap column}form{display:flex;flex-direction:column}.hero-banner{display:flex;align-items:center;flex-flow:wrap column;justify-content:flex-end;background:url(https://occhimagazine.com/wp-content/uploads/2018/02/RaidersPoster-1024x496.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;background-color:rgba(0,0,0,0.8);width:100%;height:40rem}.hero-banner>*{color:white}.hero-banner .hero-img{width:100%}.hero-banner .hero-logo{width:80%;margin:0 auto;display:flex;align-items:center;flex-flow:wrap row;justify-content:space-evenly}.hero-banner .hero-content{background-color:transparent;margin-bottom:2rem}.inedit{cursor:pointer;letter-spacing:0.2rem;padding:0.1rem 1.2rem;text-transform:uppercase;border:2px solid #fff;transition:color 0.3s ease-in-out, border-color 0.3s ease-in-out}.inedit:hover{color:yellow;border-color:yellow}.home-videos{display:flex;align-items:center;flex-flow:wrap column;justify-content:space-evenly;width:80%}.home-videos>:last-child{margin:3rem 0}.home-videos article{display:flex;align-items:center;flex-flow:wrap column;justify-content:space-evenly;width:60%}.home-videos article>:first-child{margin:1rem}.home-videos article>:last-child div{margin:0.5rem;width:20rem;height:20rem;background-color:#bcbabb;background-image:linear-gradient(to right, #bcbabb 0%, #c0c0c4 20%, #bcbabb 40%, #bcbabb 100%);background-repeat:no-repeat;animation:shimmer 1s infinite linear}.home-videos article>div{width:100%;display:flex;align-items:center;flex-flow:wrap row;justify-content:space-between}.login-page{display:flex;align-items:center;flex-flow:wrap column;justify-content:center}.myAccount-page{width:100%;justify-content:space-evenly}.myAccount-page .myAccount-subtitles{width:100%;display:flex;align-items:center;flex-flow:wrap row;justify-content:space-evenly}@media only screen and (min-width: 768px){.myAccount-page .myAccount-subtitles{width:35rem;margin-right:auto}}footer{min-height:15vh;width:100%;background-color:#bcbabb;display:flex;align-items:center;flex-flow:wrap column;justify-content:space-around;padding:2rem 0;transition:background-color 0.4s ease-in-out,color 0.4s ease-in-out}footer>:last-child{margin-top:1.5rem}footer .footer-menu{width:100%;line-height:1.8;height:10rem;color:#fff;text-transform:uppercase;letter-spacing:0.2rem;font-weight:bold;display:flex;align-items:center;flex-flow:wrap column;align-content:space-evenly;justify-content:center}footer .footer-menu *>:hover{color:yellow}@media only screen and (min-width: 768px){footer{flex-direction:row}footer .footer-menu{align-items:start;width:50%;align-content:space-around}}footer.dark-mode{background-color:#03071e}

